<template>
    <v-container class="py-8">
        <v-card :loading="loadingCard">
            <v-row >
                <v-col cols="6">
                    <v-card-title>Administración de usuarios</v-card-title>
                </v-col>
                <v-col offset="3" cols="3">
                    <v-card-title>
                        <v-btn color="success" @click="updateUsers" :loading="updateUsersLoading">Guardar cambios</v-btn>
                    </v-card-title>
                </v-col>
            </v-row>
            <v-data-table
                :headers="headers"
                :items="items"
                :items-per-page="12"
                class="elevation-1"
            >
                <template v-slot:[`item.state`]="{ item }">
                    <v-simple-checkbox
                    color="green"
                    @click="driverEnabledChanged(item)"
                    v-model="item.state"
                    ></v-simple-checkbox>
                </template>

                <template v-slot:[`item.actions`]="{ item }">
                    <v-btn icon @click="moreInfo(item)">
                        <v-icon color="blue">search</v-icon>
                    </v-btn>
                    <v-btn icon @click="editUser(item)">
                        <v-icon color="green">edit</v-icon>
                    </v-btn>
                    <v-btn icon @click="deleteUser(item)">
                        <v-icon color="red">delete</v-icon>
                    </v-btn>
                </template>
            </v-data-table>
        </v-card>
    </v-container>
</template>

<script>
import provider from '@/providers'

export default {
    name:'UserManagement-Page',
    data () {
      return {
        updateUsersLoading: false,
        usersToUpdate: [],
        loadingCard: false,
        headers: [
            {
                text: 'Email',
                value: 'email',
                align: 'center'
            },
            {
                text: 'Role',
                value: 'role',
                align: 'center'
            },
            {
                text: 'Nombre',
                value: 'name',
                align: 'center'
            },
            {
                text: 'Apellido',
                value: 'surname',
                align: 'center'
            },
            {
                text: 'Dirección',
                value: 'address',
                align: 'center'
            },
            {
                text: 'Conductor habilitado',
                value: 'state',
                align: 'center'
            },
            {
                text: 'Acciones',
                value: 'actions',
                align: 'center'
            }
        ],
        items: [
        ],
      }
    },
    async mounted(){  
        this.loadingCard = true
        try {
            const users = await provider.fetchUsers()
            users.forEach(user => {
                this.items.push({
                    id: user._id,
                    email: user.email ? user.email : '',
                    role: user.role ? user.role : '',
                    name: user.role == 'CLIENT' ? user.clientDetails.name : user.driverDetails.name,
                    surname: user.role == 'CLIENT' ? user.clientDetails.surname: user.driverDetails.surname,
                    address: user.role == 'CLIENT' ? user.clientDetails.address: user.driverDetails.address,
                    state: user.driverEnabled ? user.driverEnabled : false,
                    details: user.role == 'CLIENT ' ? user.clientDetails : user.driverDetails
                })
            })
        } catch (error) {
            console.log('error: ', error)
        }
        this.loadingCard = false
    },
    methods:{
        async updateUsers(){
            this.updateUsersLoading = true
            for(let i = 0; i<this.usersToUpdate.length; i++){
                const {id, email, role, name, surname, address, state} = this.usersToUpdate[i]
                await provider.updateUser({id, email, role, name, surname, address, state})
            }
            this.existUser = false
            this.usersToUpdate = []
            this.updateUsersLoading = false
        },
        driverEnabledChanged({id, email, role, name, surname, address, state}){
            let existUser = false
            this.usersToUpdate.forEach(async (userToUpdate, index) => {
                if(userToUpdate.id === id){
                    this.usersToUpdate[index] = {id, email, role, name, surname, address, state}
                    existUser = true
                }
            })
            if(!existUser) this.usersToUpdate.push({id, email, role, name, surname, address, state})
        },
        editUser(){
    
        },
        deleteUser(){
    
        }
    }
}
</script>

