<template>
  <v-navigation-drawer v-model="drawer" app left v-click-outside="clickOutside" class="py-4">
    <v-expansion-panels>
        <v-expansion-panel>
            <v-btn width="100%" plain class="button-text" to="/">Inicio</v-btn>
            <v-expansion-panel-header>Administración</v-expansion-panel-header>
            <v-expansion-panel-content>
                <v-btn width="100%" plain class="button-text" to="user-management">Administración de usuarios</v-btn>
            </v-expansion-panel-content>
        </v-expansion-panel>
    </v-expansion-panels>
  </v-navigation-drawer>
</template>

<script>
    export default {
        name:'MenuDrawer',
        data(){
            return{
                clickOutsideState: true
            }
        },
        props:{
            value: {
                type: Boolean,
                required: true
            }
        },
        computed:{
            drawer:{
                get(){
                    return this.value
                },
                set(value){
                    this.$emit('set-drawer', value)
                }
            }
        },
        methods:{
            clickOutside(){
                this.clickOutsideState = !this.clickOutsideState
                if(this.clickOutsideState && this.drawer){
                    this.clickOutsideState = true
                    this.drawer = false
                }
            }
        }
    }
</script>

<style scoped>
    .button-text{
        font-size: 0.8em;
    }
</style>