import axios from 'axios'

class Provider{
    constructor(){
        this.url = ''
        this.apiRoute = '/api'
        this.route = this.url + this.apiRoute
    }

    async fetchUsers(){
        console.log('llego')
        const route = this.route + '/fetch-users'
        try {
            const {data: {users}} = await axios.get(route)
            return users
        } catch (error) {
            throw new Error(error)
        }
    }

    async updateUser({id, email, role, name, surname, address, state}){
        const route = this.route + '/update-user'
        try {
            const {data: {user}} = await axios.post(route, {id, email, role, name, surname, address, state})
            return user
        } catch (error) {
            throw new Error(error)
        }
    }
}

export default new Provider